import { Component, OnInit } from '@angular/core';
import { AppService } from '../shared/app.service';
import { NgxSpinnerService } from 'ngx-spinner';
import { CoachComponent } from '../model/coach/coach.component';
import { ActivatedRoute, NavigationEnd, Router } from '@angular/router';
import { Location } from '@angular/common';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import * as $ from 'jquery';
import Swal from 'sweetalert2';
import * as moment from 'moment';

@Component({
  selector: 'app-courseon-club-edit',
  templateUrl: './courseon-club-edit.component.html',
  styleUrls: ['./courseon-club-edit.component.scss']
})
export class CourseonClubEditComponent extends CoachComponent implements OnInit {
  StartTime: any;
  EndTime: any;
  fromValidation: boolean = false;
  time: any = {
    StartTime: '',
    EndTime: '',
  }
  //public filename: string = "";
  public response: any;
  public trans_error: Boolean = false;
  public plan_error: Boolean = false;
  public availablity: any = [];
  public selectedCity: any;
  public addmore: Boolean = false;
  public modifiedbutton: boolean = true;
  public res = {
    Course_Id: "",
    Coach_Id: "",
    Description: "",
    Postalcode: "",
    Mode_of_Transport: "",
    specialty: "",
    Photo: "",
    filename: "",
    Plan: "",
    Technical_Provided: "",
    Video: "",
    Club_Name: "",
    Coach_Ville: "",
    availablity: [],
    address: ""

  }
  public transportData: any = [];
  constructor(appService: AppService,
    activatedRoute: ActivatedRoute,
    router: Router,
    location: Location,
    spinner: NgxSpinnerService,
    private modalService: NgbModal
  ) {
    super(
      activatedRoute,
      router,
      appService,
      location,
      spinner
    );
  }
  onFromTimeChange(event) {
    console.log(event.target.value)
    let time = this.availablity[0].StartTime.split(':');
    let date = moment(`2023-01-01 ${this.availablity[0].StartTime}:00`);
    let date1 = moment(`2023-01-01 ${this.availablity[0].EndTime}`);
    console.log(time, date, moment(`2023-01-01 ${this.availablity[0].StartTime}:00`));
    if (this.availablity[0].EndTime && this.availablity[0].StartTime) {
      console.log(moment(date1).isBefore(date))
      if (moment(date1).isBefore(date)) {
        this.fromValidation = true;
      }
      else {
        this.fromValidation = false;
      }
    }
  }

  onToTimeChange(event) {
    console.log(event.target.value)
    let date = moment(`2023-01-01 ${this.availablity[0].StartTime}:00`);
    let date1 = moment(`2023-01-01 ${this.availablity[0].EndTime}:00`);
    if (this.availablity[0].EndTime && this.availablity[0].StartTime) {
      if (moment(date1).isBefore(date)) {
        this.fromValidation = true;
      }
      else {
        this.fromValidation = false;
      }
    }
  }
  ngOnInit() {
    this.addmore = false;
    this.modifiedbutton = true;
    const params = this.activatedRoute.snapshot.queryParamMap["params"];
    var parameters = {
      coachId: params.coach_id,
      id: params.courseonclub_id
    };
    this.citySearch(params.code_postal);
    this.getCourseClub(parameters);
    $('#trans_error').hide();
    $('#photo_error').hide();
    $('#plan_error').hide();
    var titile = document.getElementsByClassName("brand");
    if (titile)
      titile[0].innerHTML = 'COURS COLLECTIF CLUB';
    let formInputItem = document.querySelectorAll(".form_devarea")[0].querySelectorAll("input");
    formInputItem.forEach(function (inputElement) {
      inputElement.setAttribute("disabled", "true");
    });

    let textarea = document.getElementById("exampleFormControlTextarea1") as HTMLTextAreaElement;
    textarea.setAttribute("disabled", "true");

    let elebebtn = document.querySelector("#enableBtn") as HTMLElement;
    elebebtn.textContent = "Modifier";

    elebebtn.setAttribute("data-toggle", "modal");

    let icancel = document.querySelector("#cancel") as HTMLElement;
    icancel.style.display = 'none';

    let enableBtn = document.getElementById('enableBtn') as HTMLElement;
    enableBtn.style.visibility = 'visible';

    let Enregistrer = document.getElementById('Enregistrer') as HTMLElement;
    Enregistrer.style.display = 'none';

    if (this.availablity.length == 0) {
      this.addDay();
    }
  }

  closemodel = (id: string) => {
    this.modalService.dismissAll();
    this.modifiedbutton = true;
    let modelData = document.getElementById(id);
    modelData.classList.remove("in");
    modelData.classList.add("out");
    modelData.style.display = "none";
  }

  enableForm() {
    this.modalService.dismissAll();
    this.addmore = true;
    this.modifiedbutton = false;
    $(".form_devarea :input").prop("readonly", false);
    $(".form_devarea :input").prop("required", true);

    let itransport = document.querySelectorAll(".form-check-input");
    itransport.forEach(function (checkItem) {
      checkItem.removeAttribute("disabled");
    })

    let formInputItem = document.querySelectorAll(".form_devarea")[0].querySelectorAll("input");
    formInputItem.forEach(function (inputElement) {
      inputElement.removeAttribute("disabled");
    });

    let textarea = document.getElementById("exampleFormControlTextarea1") as HTMLTextAreaElement;
    textarea.removeAttribute("disabled");

    let icancel = document.querySelector("#cancel") as HTMLElement;
    icancel.style.display = 'inline';

    let enableBtn = document.getElementById('enableBtn') as HTMLElement;
    enableBtn.style.visibility = 'hidden';

    let Enregistrer = document.getElementById('Enregistrer') as HTMLElement;
    Enregistrer.style.display = 'inline';

    let modelData = document.getElementById('myModal_edit_course');
    modelData.classList.remove("in");
    modelData.style.display = "none";
    let bodyelement = document.getElementsByClassName('modal-open');
    bodyelement[0].classList.remove("modal-open");
    let overlay = document.getElementsByClassName('modal-backdrop');
    overlay[0].remove();
  }


  getCourseClub(parameters) {
    var selectedPlan = [];
    var selectedTransport: any;
    var coach = JSON.parse(localStorage.getItem("onmytennis"));
    var coach1 = JSON.parse(coach);
    var coachid = {
      "coachId": coach1.id
    }
    this.spinner.show();
    this.appService.getAll("/course/getcousecollectiveclubbyid", parameters).subscribe((data: any) => {
      if (data.data.course.length > 0) {
        this.response = data.data.course[0];
        this.availablity = data.data.availablity;
        this.res = data.data.course[0];
        let formInputItem = document.querySelectorAll(".form_devarea")[0].querySelectorAll("input");
        selectedTransport = this.res.Mode_of_Transport.split(",");
        this.transportData = selectedTransport;
        formInputItem.forEach(function (inputElement) {
          let mode = inputElement as HTMLInputElement;
          if (mode.type == "checkbox") {
            let modechild = mode.nextSibling as HTMLElement;
            let Mode_of_Transport: string = modechild.nodeValue.trim();
            if (selectedTransport.length > 0) {
              for (var i = 0; i < selectedTransport.length; i++) {
                if (Mode_of_Transport == selectedTransport[i]) {
                  mode.checked = true;
                }
                else {
                  if (Mode_of_Transport == selectedTransport[i]) {
                    mode.checked = true;
                  }
                }
              }
            }
          }
          selectedPlan = data.data.course[0].Plan.split(",");
          if (mode.type == "radio") {
            if (selectedPlan.length > 0) {
              for (var i = 0; i < selectedPlan.length; i++) {
                if (mode.id == "inlineradio3" && selectedPlan[i] == "Commission") {
                  mode.checked = true;
                }
              }
            }
          }
        });
        this.spinner.hide();
      }
      else if (data.data.course.length == 0) {
        this.spinner.hide();
      }
    });
  }

  onSubmit(res) {
    this.spinner.show();
    $('#trans_error').hide();
    $('#plan_error').hide();
    $('#photo_error').hide();
    this.trans_error = false;
    this.plan_error = false;
    var coach = JSON.parse(localStorage.getItem("onmytennis"));
    var coach1 = JSON.parse(coach);
    res.Coach_Id = coach1.id;
    var transport = "";
    var plan = "";
    let formInputItem = document.querySelectorAll(".form_section")[0].querySelectorAll("input");
    formInputItem.forEach(function (inputElement) {
      let mode = inputElement as HTMLInputElement;
      if (mode.type == "checkbox") {
        if (mode.checked == true) {
          let modechild = mode.nextSibling as HTMLElement;
          if (transport !== "") {
            transport = transport + "," + modechild.nodeValue.trim();
          }
          else {
            transport = modechild.nodeValue.trim();
          }
        }
      }
      if (mode.type == "radio") {
        if (mode.checked == true) {
          let modechild = mode as HTMLElement;
          if (modechild.getAttribute('name') == 'sampleinlineradio1') {
            plan = plan + modechild.getAttribute('value');
          }
        }
      }
    })
    res.Plan = plan;
    if (!res.Photo) {
      $('#photo_error').show();
    }
    if (res.Mode_of_Transport == "") {
      this.trans_error = true;
      $('#trans_error').show();
    }
    else if (res.Plan == "") {
      $('#plan_error').show();
      this.plan_error = true;
    }
    this.res.availablity = this.availablity;
    if (this.trans_error == false && this.plan_error == false && res.Photo) {
      this.appService.create('/course/updatecousecollectiveclub', res).subscribe((response) => {
        if (response) {
          this._showAlertMessage('alert-success', 'Mise à jour avec succès');
          this.ngOnInit();
          this.spinner.hide();
        }
        else {
          this._showAlertMessage('alert-danger', 'Échec de la mise à jour');
          this.spinner.hide();
        }
      })
    }
    else {
      this.spinner.hide();
    }
  }


  searchCity(e) {

    if (e && e.target.value) {
      this.spinner.show();
      this.appService.getAll("/city/" + e.target.value).subscribe(
        response => {
          this.spinner.hide();
          if (response && response["data"]) {
            this.selectedCity = (response as any).data.city_list;
            if (this.selectedCity.length > 0)
              this.res.Coach_Ville = this.selectedCity[0].Code_commune_INSEE;
          }
        },
        error => { }
      );
    }
  }

  citySearch(code) {
    this.appService.getAll("/city/" + code).subscribe(
      response => {
        if (response && response["data"]) {
          this.selectedCity = (response as any).data.city_list;
          if (this.selectedCity.length > 0)
            this.res.Coach_Ville = this.selectedCity[0].Code_commune_INSEE;
        }
      },
      error => { }
    );
  }


  deleteCourse() {

    const params = this.activatedRoute.snapshot.queryParamMap["params"];
    var parameters = {
      CoachId: params.coach_id,
      Id: params.courseonclub_id,
      course: "coursecollectifclub",
    };
    this.appService
      .create("/course/deletecourse", parameters)
      .subscribe(response => {
        this.spinner.hide();
        if (response && response.isSuccess == true) {
          this._showAlertMessage("alert-success", "Supprimé avec succès");
          window.scrollTo(0, 0);
        } else {
          this.spinner.hide();
          this._showAlertMessage("alert-danger", "Échec de la mise à jour");
        }
      });

  }

  addDay(clickEvent: boolean = false) {
    if (this.addmore) {
      var coaches = JSON.parse(localStorage.getItem("onmytennis"));
      var coach = JSON.parse(coaches);
      // var id = this.availablity.length + 1;
      this.availablity.push({
        Id: 0,
        CoachId: coach.id,
        Weekday: "",
        MaxCount: "",
        StartTime: "",
        EndTime: "",
        Price: "",
        Course: ""
      })
    } else if (clickEvent) {
      this.modifiedbutton = false;
      let modelData = document.getElementById('myModal_edit_course');
      modelData.classList.remove("out");
      modelData.classList.add("in");
      modelData.style.display = "block";
    } else {
      this.modifiedbutton = true;
      this.closemodel("myModal_edit_course");
    }

  }

  removeDay(id, index) {
    this.spinner.show();
    var coaches = JSON.parse(localStorage.getItem("onmytennis"));
    var coach = JSON.parse(coaches);
    var data = {
      CoachId: coach.id,
      Id: id
    }
    this.appService.create('/course/deleteclubavailablity', data).subscribe((res) => {
      this.spinner.hide();
      this.availablity.splice(index, 1);
    })
  }

  propagateChange = (result, file, type) => {
    this.res.Photo = result;
    this.res.filename = file.name;
  };

  changeListener($event): void {
    this.readThis($event.target);
  }

  readThis(inputValue: any): void {
    var file: File = inputValue.files[0];
    $(".file-upload-wrapper").attr('data-text', file.name);
    let reader = new FileReader();


    if (file.size <= 55326 && file.type == "image/jpeg" || file.type == "image/png") {

      reader.readAsDataURL(file);
      reader.onload = _event => {
        this.propagateChange(reader.result, file, file.type);
      };

    }
    else {
      $('#file-upload-field').empty();
      $("#file-upload-field").trigger("change");
      $(".file-upload-wrapper").attr('data-text', '');
      Swal.fire({

        icon: "error",
        title: "Erreur",
        text: 'Veuillez télécharger uniquement des fichiers portant les extensions .jpeg/.jpg/.png/.jfif et dont la taille est inférieure à 54KB.',
        confirmButtonText: "OUI",
      });
      reader.onerror = function (error) {
        console.log("Error: ", error);
      };
      return;
    }

  }

  download() {
    if (this.res.Photo) {
      var blob = this.dataURLtoBlob(this.res.Photo);
      var link = document.createElement("a");
      link.href = window.URL.createObjectURL(blob);
      link.download = this.res.filename;
      link.click();
    }
  }

  dataURLtoBlob(dataurl) {
    var arr = dataurl.split(",");
    var mime = arr[0].match(/:(.*?);/)[1];
    var bstr = window.atob(arr[1]);
    var n = bstr.length;
    var u8arr = new Uint8Array(n);

    while (n--) {
      u8arr[n] = bstr.charCodeAt(n);
    }
    return new Blob([u8arr], {
      type: mime
    });
  }

  transport_changed(event: any) {
    this.transportData = this.appService.transportChanged(event, this.transportData);
    this.res.Mode_of_Transport = this.transportData.join();
    if (this.res.Mode_of_Transport == "") {
      this.trans_error = true;
      $('#trans_error').show();
    } else {
      this.trans_error = false;
      $('#trans_error').hide();
    }
  }
}
