import {
  Component,
  OnInit,
  AfterViewChecked,
  EventEmitter,
  Output,
  Input
} from "@angular/core";
import {
  FormsModule,
  FormGroup,
  FormControl,
  FormBuilder,
  Validators,
  ReactiveFormsModule,
  FormArray
} from "@angular/forms";
import { Location } from "@angular/common";
import { BrowserModule, Title, Meta } from "@angular/platform-browser";
import { ActivatedRoute, NavigationEnd, Router } from "@angular/router";
import { AppService } from "../../shared/app.service";
import { AppComponent } from "../../app.component";
import { ConfirmedValidator } from "../../confirmed.validator";
import { NewConfirmedValidator } from "../../confirmed.validators";
/* [ Spinner ] */
import { NgxSpinnerService } from "ngx-spinner";
import { async } from "q";

@Component({
  selector: "app-coach-password",
  templateUrl: "./coach-password.component.html",
  styleUrls: ["./coach-password.component.scss"]
})
export class CoachPasswordComponent extends AppComponent implements OnInit {
  public changePasswordForm: FormGroup;

  show: boolean = false;
  show1: boolean = false;
  show2: boolean = false;
  showPassword = false;

  constructor(
    private formBuilder: FormBuilder,
    activatedRoute: ActivatedRoute,
    router: Router,
    appService: AppService,
    location: Location,
    spinner: NgxSpinnerService
  ) {
    super(activatedRoute, router, appService, location, spinner);
  }
  //Function for Init
  ngOnInit() {
    this.show = false;
    this.changePasswordForm = this.formBuilder.group(
      {
        password: [
          "",
          [
            Validators.required,
            Validators.minLength(8)
          ]
        ],
        newPassword: [
          "",
          [
            Validators.required,
            Validators.minLength(8)
          ]
        ],
        confirmPassword: ["", [Validators.required, Validators.minLength(8)]]
      },
      {
        validator: [ConfirmedValidator('newPassword', 'confirmPassword')]
      },
    );
  }
  //Funtion for enable and disable
  makeEnable() {
    console.log('testing-prod');
  }
  /* [ Form controls ] */
  get cForm() {
    return this.changePasswordForm.controls;
  }
  //Function for change password
  changePassword(isValid) {
    if (isValid.status === "VALID") {
      const userData = JSON.parse(this._setSession("getItem"));
    
      let value = {
        email: userData["email"],
        role: userData["roleId"],
        password: this.changePasswordForm.value.newPassword
      };
      this.appService.create("/user/setNewPassword", value).subscribe(
        response => {
          if (response && response.isSuccess == true) {
            isValid.reset();
            this._showAlertMessage(
              "alert-success",
              "Le mot de passe a été changé avec succès"
            );
          } else {
            this._showAlertMessage(
              "alert-danger",
              "Impossible de continuer. S'il vous plaît essayer après un certain temps"
            );
          }
          this.spinner.hide();
        },
        error => { }
      );
    }
  }
  // }
// Funtion for click events
  onClickPasswordfield() {
    this.show = !this.show;
  }

  onClickPasswordfield1() {
    this.show1 = !this.show1;
  }
  onClickPasswordfield2() {
    this.show2 = !this.show2;
  }
}
