  constructor(
    activatedRoute: ActivatedRoute,
    router: Router,
    appService: AppService,
    location: Location,
    spinner: NgxSpinnerService,
    private elem: ElementRef
  ) {
    super(activatedRoute, router, appService, location, spinner);
    this.router.routeReuseStrategy.shouldReuseRoute = () => false;
  }
  // Initial load
  ngOnInit() {
    this.getBookingDetails();
    this.href = this.router.url;

    if (this.href == "/coach/Stage") {
      this.stageClass = true;
      this.tourClass = false;
      this.animationClass = false;
      this.individualClass = false;
      this.coursedemandClass = false;
      this.courseonclubClass = false;
      this.teambuildingClass = false;

    } else if (this.href == "/coach/Animation") {
      this.stageClass = false;
      this.tourClass = false;
      this.animationClass = true;
      this.individualClass = false;
      this.coursedemandClass = false;
      this.courseonclubClass = false;
    } else if (this.href == "/coach/Tounament") {
      this.stageClass = false;
      this.tourClass = true;
      this.animationClass = false;
      this.individualClass = false;
      this.coursedemandClass = false;
      this.courseonclubClass = false;
      this.teambuildingClass = false;
    }
    else if (this.href == "/coach/individual-course") {
      this.individualClass = true;
      this.stageClass = false;
      this.tourClass = true;
      this.animationClass = false;
      this.coursedemandClass = false;
      this.courseonclubClass = false;
      this.teambuildingClass = false;
    }
    else if (this.href == "coach/CourseonDeman") {
      this.coursedemandClass = true;
      this.courseonclubClass = false;
      this.individualClass = false;
      this.stageClass = false;
      this.tourClass = true;
      this.animationClass = false;
      this.teambuildingClass = false;
    }
    else if (this.href == "coach/CourseOnClub") {
      this.courseonclubClass = true;
      this.coursedemandClass = false;
      this.individualClass = false;
      this.stageClass = false;
      this.tourClass = false;
      this.animationClass = false;
      this.teambuildingClass = false;
    }
    else if (this.href == "coach/team-building") {
      this.teambuildingClass = true;
      this.coursedemandClass = false;
      this.courseonclubClass = false;
      this.individualClass = false;
      this.stageClass = false;
      this.tourClass = true;
      this.animationClass = false;
    }



    var coach = JSON.parse(localStorage.getItem("onmytennis"));
    if (coach) {
      var coach1 = JSON.parse(coach);
      if (coach1.roleId == 2) {
        this.spinner.show();
        var Coach_Email = {
          Coach_Email: coach1.email
        };
        var coachid = {
          coachId: coach1.id
        };
        this.appService
          .create("/coach/get_coach_by_id", Coach_Email)
          .subscribe((data: any) => {
            if (data.isSuccess == true) {
              if (this.alive) {
                this.getStage(coachid);
                this.gettournament(coachid);
                this.getanimation(coachid);
                this.getindividual(coachid);
                this.getcoursecollection(coachid);
                this.getteambuilding(coachid);
                this.getcourseonclub(coachid);
                this.spinner.hide();
              }

              if (data.data.coach_list) {
                this.image = data.data.coach_list[0].Coach_Image;
                if (this.image == null) {
                  this.image =
                    "../../../assets/images/img_avatar.png";
                }
              }
              this.spinner.hide();
            } else {
              this.spinner.hide();
            }
          });
      }
      if (coach1.firstName + " " + coach1.lastName)
        this.username = this.capitalizeFLetter(
          coach1.firstName + " " + coach1.lastName
        );
    }
  }
