  // Function get animation.
  getanimation(coachid) {
    this.appService
      .getAll("/course/getAnimationCourseLeft", coachid)
      .subscribe(response => {
        if ((response as any).data.course.length > 0) {
          if (response && response["data"]) {
            this.resStage = (response as any).data.course;
            let animationData = this.resStage.map((value, key) => {
              return {
                title: value.animations_name,
                stage_id: value.id,
                coach_id: value.Coach_Id,
                code_postal: value.Postalcode,
                iclass: "fa fa-th-large",
                style: false,
                path: "/coach/Stage"
              };
            });
            this.animationList = animationData;
          }
        }
      });
  }
  // Function get coursecollection
  getcoursecollection(coachid) {
    this.appService
      .getAll("/course/getcoursecollectionleftmenu", coachid)
      .subscribe(response => {
        if ((response as any).data.course.length > 0) {
          if (response && response["data"]) {
            this.res = (response as any).data.course;
            let indvData = this.res.map(value => {
              return {
                title: value.Eventname,
                coursedemand_id: value.Group_Id,
                coach_id: value.Coach_Id,
                code_postal: value.Postalcode,
                iclass: "fa fa-th-large",
                style: false,
                path: "/coach/CourseonDemanedit"
              };
            });
            this.coursedemandList = indvData;
          }
        }
      });
  }

  // Function get backend to show course detail on menu
