  capitalizeFLetter(name) {
    return name[0].toUpperCase() + name.slice(1);
  }
  //Function for redirect Menu
  redirectMenu(link: any) {
    alert(link);
  }
  //Get booking details
  getBookingDetails() {
    var coach = JSON.parse(localStorage.getItem("onmytennis"));

    if (coach) {
      var coach1 = JSON.parse(coach);

      this.roleid = coach1.roleId;
      if (coach1.roleId == 2) {

        var coachdetails = {
          Coach_ID: coach1.id
        };
        this.appService.getAll("/user/getallbookings", coachdetails).subscribe(response => {
          if (response && response["data"]) {
            this.count_coach_id = response["data"].length;
          }

        });

      }
    }

  }

  bookingclick() {
    var coach = JSON.parse(localStorage.getItem("onmytennis"));
    if (coach) {
      var coach1 = JSON.parse(coach);
      this.roleid = coach1.roleId;

      if (this.roleid == 2) {
        var coachDetails = {
          markreadCoach: 1,
          Coach_ID: coach1.id

        };

        this.appService.create("/user/updateallBookings", coachDetails).subscribe((response: any) => {


        });

      }

    }

  }
  //Function for delete account
  _delete_account() {
    var coach = JSON.parse(localStorage.getItem("onmytennis"));
    if (coach) {
      var coach1 = JSON.parse(coach);
      var emailId = {
        email: coach1.email
      };
      this.spinner.show();

      this.appService
        .create("/user/accountdeletebyemail", emailId)
        .subscribe((data: any) => {
          if (data.isSuccess == true) {
            this.spinner.hide();
            this._setSession("removeItem");
            this._gotoPath("/");
          } else {
            this.spinner.hide();
          }
        });
    }
  }

