  addDay() {

    var coaches = JSON.parse(localStorage.getItem("onmytennis"));
    var coach = JSON.parse(coaches);
    this.availablity.push({
      Id: 0,
      CoachId: coach.id,
      Weekday: "",
      MaxCount: "",
      StartTime: "",
      EndTime: "",
      Price: "",
      Course: ""
    })
  }

  removeDay(id, index) {
    this.spinner.show();
    var coaches = JSON.parse(localStorage.getItem("onmytennis"));
    var coach = JSON.parse(coaches);
    var data = {
      CoachId: coach.id,
      Id: id
    }
    this.appService.create('/course/deleteclubavailablity', data).subscribe((res) => {
      this.spinner.hide();
      this.availablity.splice(index, 1);
    })
  }

  propagateChange = (result, file, type) => {

    this.res.Photo = result;
    this.res.filename = file.name;
    console.log('filename ' + this.res.filename);
    //$('#photo_error').hide();
  };

  changeListener($event): void {
    this.readThis($event.target);
  }

  readThis(inputValue: any): void {
    var file: File = inputValue.files[0];
    $(".file-upload-wrapper").attr('data-text', file.name);

    let reader = new FileReader();

    if (file.size <= 55326 && file.type == "image/jpeg" || file.type == "image/png") {

      reader.readAsDataURL(file);
      reader.onload = _event => {
        this.propagateChange(reader.result, file, file.type);
      };

    }
    else {
      $('#file-upload-field').empty();
      $("#file-upload-field").trigger("change");
      $(".file-upload-wrapper").attr('data-text', '');
      Swal.fire({

        icon: "error",
        title: "Erreur",
        text: 'Veuillez télécharger uniquement des fichiers portant les extensions .jpeg/.jpg/.png/.jfif et dont la taille est inférieure à 54KB.',
        confirmButtonText: "OUI",
      });
      reader.onerror = function (error) {
        console.log("Error: ", error);
      };
      return;
    }

  }

  download() {
    if (this.res.Photo) {
      var blob = this.dataURLtoBlob(this.res.Photo);
      var link = document.createElement("a");
      link.href = window.URL.createObjectURL(blob);
      link.download = this.res.filename;
      link.click();
    }
  }

  dataURLtoBlob(dataurl) {
    var arr = dataurl.split(",");
    var mime = arr[0].match(/:(.*?);/)[1];
    var bstr = window.atob(arr[1]);
    var n = bstr.length;
    var u8arr = new Uint8Array(n);

    while (n--) {
      u8arr[n] = bstr.charCodeAt(n);
    }
    return new Blob([u8arr], {
      type: mime
    });
  }

  transport_changed(event: any) {
    this.transportData = this.appService.transportChanged(event, this.transportData);
    this.res.Mode_of_Transport = this.transportData.join();
    if (this.res.Mode_of_Transport == "") {
      this.trans_error = true;
      $('#trans_error').show();
    } else {
      this.trans_error = false;
      $('#trans_error').hide();
    }
  }
  numberOnly(event): boolean {
    const charCode = (event.which) ? event.which : event.keyCode;
    if (charCode > 31 && (charCode < 48 || charCode > 57)) {
      return false;
    }
    return true;

  }

