import { Component, OnInit, AfterViewChecked } from "@angular/core";
import { Location } from "@angular/common";
import { BrowserModule, Title, Meta } from "@angular/platform-browser";
import { ActivatedRoute, NavigationEnd, Router } from "@angular/router";
import { AppService } from "../../shared/app.service";
import { CoachComponent } from "./../../model/coach/coach.component";
import * as $ from "jquery";
import { NgxSpinnerService } from "ngx-spinner";
import Swal from 'sweetalert2';
@Component({
  selector: "app-profile",
  templateUrl: "./profile.component.html",
  styleUrls: ["./profile.component.scss"]
})
export class ProfileComponent extends CoachComponent implements OnInit {
  public res = {
    Coach_Fname: "",
    Coach_Lname: "",
    Coach_Email: "",
    Coach_Phone: "",
    InstagramURL: "",
    FacebookURL: "",
    TwitterURL: "",
    Coach_Description: "",
    Coach_Rayon: 1,
    Coach_Ville: "",
    Coach_Emplacement: "",
    Coach_Price: "",
    Coach_City: "",
    Coach_Services: "",
    Coach_PriceX10: "",
    Coach_Bank_Name: "",
    Coach_Bank_ACCNum: "",
    Branch_Code: "",
    Coach_Bank_City: "",
    Coach_Image: "../../assets/images/profile_blackwhite.png",
    Coach_Resume: ""
  };

  public selectedCity: any = null;
  public cityId: any = "";

  public trans_error: Boolean = false;
  public cheque_error: Boolean = false;
  image: any;
  public activeTabIndex = 0;
  public Coach_Phone = 0;
  public submit_disabled = true;
  public response: any;
  public filename: string = "";
  selectedFile: File;

  constructor(
    activatedRoute: ActivatedRoute,
    router: Router,
    appService: AppService,
    location: Location,
    spinner: NgxSpinnerService
  ) {
    super(activatedRoute, router, appService, location, spinner);
  }
  // Initial load
  ngOnInit() {
    $("#trans_error").hide();
    $("#cheque_error").hide();
    var titile = document.getElementsByClassName("brand");
    if (titile) titile[0].innerHTML = "MON COMPTE";

    this.profileUpdate();

    let formInputItem = document.querySelectorAll(".form_devarea")[0].querySelectorAll("input");
    formInputItem.forEach(function (inputElement) {
      inputElement.setAttribute("disabled", "true");
    });
  }

  onFileSelected(event) {
    this.selectedFile = event.target.files[0];
    this.onUpload();
  }
  //Function for upload
  private onUpload() {
    const fd = new FormData();
    fd.append("myfile", this.selectedFile);
  }

  propagateChange = (result, file, type) => {
    var docx =
      "application/vnd.openxmlformats-officedocument.wordprocessingml.document";
    var doc = "application/msword";
    if (type == "application/pdf" || type == docx || type == doc) {
      this.res.Coach_Resume = result;
      this.filename = file.name;
    } else {
      this.res.Coach_Image = result;
    }
  };

  changeListener($event): void {
    this.readThis($event.target);
  }
  //Function read file extensions
  readThis(inputValue: any): void {
    var file: File = inputValue.files[0];
    var fieldname = inputValue.name;
    if (fieldname == 'myfile') {
      var reader = new FileReader();
      if (file.size <= 34887 && file.type == "image/jpeg" || file.type == "image/png") {
        reader.readAsDataURL(file);
        reader.onload = _event => {
          this.propagateChange(reader.result, file, file.type);
        };
      }
      else {
        $("input[type='file']").val('');
        Swal.fire({

          icon: "error",
          title: "Erreur",
          text: 'Veuillez télécharger uniquement des fichiers portant les extensions .pdf/.doc/.docx et dont la taille est inférieure à 33KB.',
          confirmButtonText: "OUI",
        });
      }
    } else {

      var reader = new FileReader();
      if (file.size <= 54887 && file.type == "application/pdf" || file.type == "application/doc" || file.type == "application/ms-doc" || file.type == "application/msword" || file.type == "application/vnd.openxmlformats-officedocument.wordprocessingml.document") {
        reader.readAsDataURL(file);
        reader.onload = _event => {
          this.propagateChange(reader.result, file, file.type);
        };
      }
      else {
        $("input[type='file']").val('');
        Swal.fire('Erreur', 'Veuillez télécharger uniquement des fichiers portant les extensions .pdf/.doc/.docx et dont la taille est inférieure à 50KB.', 'error');
        return;
      }

    }

  }

  //Function for tabs
  profileTabs() {
    return [
      {
        title: "Informations personnelles"
      },
      {
        title: "Ensemble de competences"
      },
      {
        title: "Details de la transaction"
      }
    ];
  }

  changeTabs(e, i) {
    this.activeTabIndex = i;
  }
  //Make enable and disbles
  makeEnable(tab) {
    $("#submit").prop("readonly", false);
    this.submit_disabled = false;
    $("#email").prop("disabled", true);
    $("#Coach_Resume").prop("disabled", false);
    $("#Coach_Resume").prop("readonly", false);
    $("#exampleFormControlSelect1").prop("disabled", false);
    $("#Coach_City").prop("disabled", false);
    $("#exampleFormControlTextarea1").prop("disabled", false);
    $(".myfile").prop("disabled", false);
    if (tab == "0" || tab == 0) {
      $(".form-group :input").prop("readonly", false);
      $(".form-group :input").prop("required", true);
      $("#submit").prop("disabled", false);

    } else if (tab == '1' || tab == 1) {
      $(".form-group :input").prop("readonly", false);
      $(".form-group :input").prop("required", true);
      $("#submit1").removeAttr("disabled");
      $("#submit1").prop("disabled", false);

    } else {
      $(".form-group :input").prop("readonly", false);
      $(".form-group :input").prop("required", true);
      $("#submit2").prop("disabled", false);
    }

    let formInputItem = document.querySelectorAll(".form_devarea")[0].querySelectorAll("input");
    formInputItem.forEach(function (inputElement) {
      inputElement.removeAttribute("disabled");
    });

  }
  //Search city
  searchCity(e) {
    if (e && e.target.value) {
      this.spinner.show();
      this.appService.getAll("/city/" + e.target.value).subscribe(
        response => {
          if (response && response["data"]) {
            this.spinner.hide();
            this.selectedCity = (response as any).data.city_list;
            if (this.selectedCity.length > 0)
              this.res.Coach_City = this.selectedCity[0].Code_commune_INSEE;
          }
        },
        error => { }
      );
    }
  }
  //Search citywith code

  citySearch(code) {
    this.spinner.show();
    this.appService.getAll("/city/" + code).subscribe(
      response => {
        this.spinner.hide();
        if (response && response["data"]) {
          this.selectedCity = (response as any).data.city_list;
          if (this.selectedCity.length > 0)
              this.res.Coach_City = this.selectedCity[0].Code_commune_INSEE;
        }
      },
      error => { }
    );
  }
  //Function in submmit
  onSubmit(res) {
    this.spinner.show();
    $("#trans_error").hide();
    $("#cheque_error").hide();
    this.trans_error = false;
    this.cheque_error = false;
    var service = "";
    var transport = "";
    var payment = "";
    let formInputItem = document
      .querySelectorAll(".tab_accordian")[0]
      .querySelectorAll("input");
    formInputItem.forEach(function (inputElement) {
      let mode = inputElement as HTMLInputElement;
      if (mode.type == "checkbox") {
        if (mode.checked == true) {
          let modechild = mode as HTMLElement;
          if (modechild.getAttribute("name") == "payment") {
            payment = payment + "," + modechild.getAttribute("id");
          } else if (modechild.getAttribute("name") == "service") {
            service = service + "," + modechild.getAttribute("id");
          } else if (modechild.getAttribute("name") == "transport") {
            transport = transport + "," + modechild.getAttribute("id");
          }
        }
      }
    });
    //transport
    if (transport[0] == ",") {
      res.Coach_transport = transport.substring(1);
    } else {
      res.Coach_transport = transport;
    }

    //payment
    if (payment[0] == ",") {
      res.Coach_payment_type = payment.substring(1);
    } else {
      res.Coach_payment_type = payment;
    }

    //service
    if (service[0] == ",") {
      res.Coach_Services = service.substring(1);
    } else {
      res.Coach_Services = service;
    }
    res.ResumeName = this.filename;
    if (this.activeTabIndex == 1) {
      if (res.Coach_transport == "") {
        this.trans_error = true;
        $("#trans_error").show();
      }
      if (this.activeTabIndex == 1 && this.trans_error == false) {

        this.appService
          .create("/coach/updateProfileTab2", res)
          .subscribe(response => {
            this.spinner.hide();
            if (response && response.isSuccess == true) {
              if (this.activeTabIndex == 1) {

                this._showAlertMessage(
                  "alert-success",
                  "Mise a jour avec succès"
                );
                this.activeTabIndex = this.activeTabIndex + 1;
              }
            } else {
              this._showAlertMessage("alert-danger", "Échec de la mise à jour");
            }
          });
      } else {
        this._showAlertMessage(
          "alert-danger",
          "champ obligatoire ne peut pas être vide"
        );
        this.spinner.hide();
      }
    } else if (this.activeTabIndex == 0) {
      this.appService
        .create("/coach/updateprofile", res)
        .subscribe(response => {
          this.spinner.hide();
          if (response && response.isSuccess == true) {
            if (this.activeTabIndex == 0) {
              this.profileUpdate();
              this._showAlertMessage("alert-success", "Mise a jour avec succès");
              this.activeTabIndex = this.activeTabIndex + 1;
            }
          } else {
            this._showAlertMessage("alert-danger", "Échec de la mise à jour");
          }
        });
    } else if (this.activeTabIndex == 2) {
      if (res.Coach_payment_type == "") {
        $("#cheque_error").show();
        this.cheque_error = true;
      }
      if (this.activeTabIndex == 2 && this.cheque_error == false) {
        this.appService
          .create("/coach/updateProfileTab3", res)
          .subscribe(response => {
            this.spinner.hide();
            if (response && response.isSuccess == true) {
              if (this.activeTabIndex == 2) {

                this._showAlertMessage(
                  "alert-success",
                  "Mise à jour avec succés"
                );
              }
            } else {
              this._showAlertMessage("alert-danger", "Échec de la mise à jour");
            }
          });
      } else {
        this._showAlertMessage(
          "alert-danger",
          "champ obligatoire ne peut pas être vide"
        );
        this.spinner.hide();
      }
    }
  }
  //Function for profile update
  profileUpdate() {
    var selectedServicesList = [];
    var selectedTransportList = [];
    var selectedPaymentList = [];
    var coach = JSON.parse(localStorage.getItem("onmytennis"));
    var coach1 = JSON.parse(coach);
    var coachemail = {
      Coach_Email: coach1.email
    };
    setTimeout(() => {
      this.spinner.show();
      this.appService
        .create("/coach/get_coach_by_id", coachemail)
        .subscribe((data: any) => {
          if (data.isSuccess == true) {
            this.spinner.hide();
          } else {
            this.spinner.hide();
          }
          let codePostal = data.data.coach_list[0].Coach_Ville;
          this.citySearch(codePostal);
          this.response = data.data.coach_list[0];
          this.res = data.data.coach_list[0];
          if (this.res.Coach_Image == null) {
            this.res.Coach_Image = "../../assets/images/profile_blackwhite.png";
          }
          this.filename = data.data.coach_list[0].ResumeName;
          selectedServicesList = data.data.coach_list[0].Coach_Services.split(
            ","
          );
          selectedTransportList = data.data.coach_list[0].Coach_transport.split(
            ","
          );
          selectedPaymentList = data.data.coach_list[0].Coach_payment_type.split(
            ","
          );
          if (selectedServicesList.length > 0) {
            for (var i = 0; i < selectedServicesList.length; i++) {
              if (selectedServicesList[i] !== "") {
                var element = <HTMLInputElement>(
                  document.getElementById(selectedServicesList[i])
                );
                element.checked = true;
              }
            }
          }
          if (selectedTransportList.length > 0) {
            for (var i = 0; i < selectedTransportList.length; i++) {
              if (selectedTransportList[i] !== "") {
                var ele = <HTMLInputElement>(
                  document.getElementById(selectedTransportList[i])
                );
                if (ele != null) {
                  ele.checked = true;
                }
              }
            }
          }
          if (selectedPaymentList.length > 0) {
            for (var i = 0; i < selectedPaymentList.length; i++) {
              if (selectedPaymentList[i] !== "") {
                var elem = <HTMLInputElement>(
                  document.getElementById(selectedPaymentList[i])
                );
                elem.checked = true;
              }
            }
          }
        });
    }, 1000);
  }
  //Function for downloa
  download() {
    if (this.res.Coach_Resume) {
      var blob = this.dataURLtoBlob(this.res.Coach_Resume);
      var link = document.createElement("a");
      link.href = window.URL.createObjectURL(blob);
      link.download = this.filename;
      link.click();
    }
  }


  numberOnly(event): boolean {
    if (event.target.value.length > 9) {
      return false;
    }
    const charCode = event.which ? event.which : event.keyCode;
    if (charCode > 31 && (charCode < 48 || charCode > 57)) {
      return false;
    }
    return true;
  }



  // numberOnly2(event): boolean {
  //   if (event.target.value.length > 33) {
  //     return false;
  //   }
  //   return true;
  // }
  numberOnly2(event): boolean {
    if (event.target.value.length > 34) {
      return false;
    }
    const charCode = event.which ? event.which : event.keyCode;
    if (charCode > 31 && (charCode < 48 || charCode > 57)) {
      return false;
    }
    return true;
  }

  dataURLtoBlob(dataurl) {
    var arr = dataurl.split(",");
    var mime = arr[0].match(/:(.*?);/)[1];
    var bstr = window.atob(arr[1]);
    var n = bstr.length;
    var u8arr = new Uint8Array(n);

    while (n--) {
      u8arr[n] = bstr.charCodeAt(n);
    }
    return new Blob([u8arr], {
      type: mime
    });
  }
}
