
import { Component, OnInit } from "@angular/core";
import { AppService } from "../shared/app.service";
import { from } from "rxjs";
import { NgxSpinnerService } from "ngx-spinner";
import { ActivatedRoute, Router } from "@angular/router";
import { CoachComponent } from "src/app/model/coach/coach.component";
import { Location } from "@angular/common";
// import { $ } from 'protractor';
import * as $ from "jquery";
//import { getSupportedInputTypes } from "@angular/cdk/platform";
import * as moment from "moment";

@Component({
  selector: 'app-commentaires',
  templateUrl: './commentaires.component.html',
  styleUrls: ['./commentaires.component.scss']
})
export class CommentairesComponent extends CoachComponent implements OnInit {

  cutomerReviewCollection: any = [];
  comments_id: number;
  public p: any = "";
  constructor(
    activatedRoute: ActivatedRoute,
    router: Router,
    appService: AppService,
    location: Location,
    spinner: NgxSpinnerService
  ) {
    super(activatedRoute, router, appService, location, spinner);
  }

  ngOnInit() {

    this.spinner.show();
    this.getReviewDetails();

    var titile = document.getElementsByClassName("brand");
    if (titile)
      titile[0].innerHTML = 'COMMENTAIRES';
  }

  getReviewDetails() {

    var coach = JSON.parse(localStorage.getItem("onmytennis"));
    var coach1 = JSON.parse(coach);
    var coachid = {
      Coach_Id: coach1.id
    };
    this.appService.getAll("/user/getallreviews", coachid).subscribe(response => {
      if (response && response["data"]) {
        for (var i = 0; i < response["data"].length; i++) {

          this.cutomerReviewCollection.push({
            commentsid: response["data"][i].commentsId,
            comments: response["data"][i].comments,
            first_name: response["data"][i].firstName,
            last_name: response["data"][i].lastName,
            course: response["data"][i].course,
            review_date: moment(response["data"][i].created_at).format("YYYY-MM-DD")
          });

        }
      }
      this.spinner.hide();

    });
  }
  commentairesClick(id) {
    this.comments_id = id;
  }
  deleteCourse(comeentsid: number) {
    var coach = JSON.parse(localStorage.getItem("onmytennis"));
    var coach1 = JSON.parse(coach);
    var comments = {
      CoachId: coach1.id,
      Id: comeentsid
    };
    this.appService
      .create("/course/deletecommentaires", comments)
      .subscribe(response => {
        this.spinner.hide();
        if (response && response.isSuccess == true) {

          this._showAlertMessage("alert-success", "Supprimé avec succès");
          window.scrollTo(0, 0);

        } else {
          this.spinner.hide();
          this._showAlertMessage("alert-danger", "Échec de la mise à jour");
        }
      });

  }

}
