import { Component, OnInit } from "@angular/core";
import { AppService } from "../../shared/app.service";
import { CoachComponent } from "src/app/model/coach/coach.component";
import { Location } from "@angular/common";
import { ActivatedRoute, NavigationEnd, Router } from "@angular/router";
import { NgxSpinnerService } from "ngx-spinner";
import * as $ from "jquery";
import Swal from 'sweetalert2';

@Component({
  selector: "app-course-collection-edit",
  templateUrl: "./course-collection-edit.component.html",
  styleUrls: ["./course-collection-edit.component.scss"]
})
export class CourseCollectionEditComponent extends CoachComponent
  implements OnInit {
  public trans_error: Boolean = false;
  public plan_error: Boolean = false;
  public selectedCity: any = null;
  public res = {
    Group_Id: "",
    Coach_ID: "",
    Min_People: "",
    Max_People: "",
    Eventname: "",
    Description: "",
    Coach_Ville: "",
    Postalcode: "",
    Photo: "",
    filename: "",
    Mode_of_transport: "",
    specialty: "",
    Price_2pl_1hr: "",
    Price_3pl_1hr: "",
    Price_4pl_1hr: "",
    Price_5pl_1hr: "",
    Price_6pl_1hr: "",
    Price_2pl_10hr: "",
    Price_3pl_10hr: "",
    Price_4pl_10hr: "",
    Price_5pl_10hr: "",
    Price_6pl_10hr: "",
    Plan: " ",
    address: ""

  };
  public transportData: any = [];
  constructor(
    activatedRoute: ActivatedRoute,
    router: Router,
    appService: AppService,
    location: Location,
    spinner: NgxSpinnerService
  ) {
    super(activatedRoute, router, appService, location, spinner);
  }

  // Initial load
  ngOnInit() {

    const params = this.activatedRoute.snapshot.queryParamMap["params"];
    var parameters = {
      coachId: params.coach_id,
      id: params.coursedemand_id
    };

    this.citySearch(params.code_postal);

    $("#trans_error").hide();
    $("#plan_error").hide();
    $('#photo_error').hide();
    var titile = document.getElementsByClassName("brand");
    if (titile) titile[0].innerHTML = "COURS COLLECTIF ONDEMAND";
    this.getDemandCourse(parameters);

    let formInputItem = document
      .querySelectorAll(".form_devarea")[0]
      .querySelectorAll("input");
    formInputItem.forEach(function (inputElement) {
      inputElement.setAttribute("disabled", "true");
    });

    let textare1 = document.getElementById(
      "exampleFormControlTextarea1"
    ) as HTMLTextAreaElement;
    textare1.setAttribute("disabled", "true");

    let textare2 = document.getElementById(
      "exampleFormControlTextarea2"
    ) as HTMLTextAreaElement;
    //textare2.setAttribute("disabled", "true");

    let elebebtn = document.querySelector("#enableBtn") as HTMLElement;
    elebebtn.style.display = "inline";

    elebebtn.setAttribute("data-toggle", "modal");

    let icancel = document.querySelector("#cancel") as HTMLElement;
    icancel.style.display = "none";

    let Enregistrer = document.querySelector("#Enregistrer") as HTMLElement;
    Enregistrer.style.display = "none";
    window.scrollTo(0, 0);
  }

  //Get course
  getDemandCourse(parameters) {
    this.spinner.show();
    var selectedPlan: any;
    var selectedTransport: any;
    var coach = JSON.parse(localStorage.getItem("onmytennis"));
    var coach1 = JSON.parse(coach);
    var coachid = {
      Coach_ID: coach1.id
    };
    this.appService
      .getAll("/course/getcouseCollectiveDemanadbyid", parameters)
      .subscribe(response => {
        if ((response as any).data.course.length > 0) {
          if (response && response["data"]) {
            this.res = (response as any).data.course[0];
            selectedTransport = (response as any).data.course[0].Mode_of_transport.split(
              ","
            );
            this.transportData = selectedTransport;
            let formInputItem = document
              .querySelectorAll(".form_devarea")[0]
              .querySelectorAll("input");
            formInputItem.forEach(function (inputElement) {
              let mode = inputElement as HTMLInputElement;
              if (mode.type == "checkbox") {
                let modechild = mode.nextSibling as HTMLElement;
                let Mode_of_Transport: string = modechild.nodeValue.trim();
                if (selectedTransport.length > 0) {

                  for (var i = 0; i < selectedTransport.length; i++) {
                    if (Mode_of_Transport == selectedTransport[i]) {
                      mode.checked = true;
                    } else {
                      if (Mode_of_Transport == selectedTransport[i]) {
                        mode.checked = true;
                      }
                    }
                  }
                }
              }
              selectedPlan = (response as any).data.course[0].Plan.split(",");
              if (mode.type == "radio") {
                if (selectedPlan.length > 0) {
                  for (var i = 0; i < selectedPlan.length; i++) {
                    if (
                      mode.id == "inlineradio3" &&
                      selectedPlan[i] == "Commission"
                    ) {
                      mode.checked = true;
                    } else if (
                      mode.id == "inlineradio4" &&
                      selectedPlan[i] == "Abonnement"
                    ) {
                      mode.checked = true;
                    }
                  }
                }
              }
            });

          }
          if ((response as any).isSuccess == true) {
            this.spinner.hide();
          } else if ((response as any).isSuccess == false) {
            this.spinner.hide();
          }
        } else {
          this.spinner.hide();
        }
      });
  }

  //Search city
  searchCity(e) {

    if (e && e.target.value) {
      this.spinner.show();
      this.appService.getAll("/city/" + e.target.value).subscribe(
        response => {
          this.spinner.hide();
          if (response && response["data"]) {
            this.selectedCity = (response as any).data.city_list;
            if (this.selectedCity.length > 0)
              this.res.Coach_Ville = this.selectedCity[0].Code_commune_INSEE;
          }
        },
        error => { }
      );
    }
  }
  //Search city with given code
  citySearch(code) {
    this.appService.getAll("/city/" + code).subscribe(
      response => {
        if (response && response["data"]) {
          this.selectedCity = (response as any).data.city_list;
          if (this.selectedCity.length > 0)
          this.res.Coach_Ville = this.selectedCity[0].Code_commune_INSEE;
        }
      },
      error => { }
    );
  }
  //Function for subnmit
  onSubmit(res) {
    $("#trans_error").hide();
    $("#plan_error").hide();
    $('#photo_error').hide();
    this.trans_error = false;
    this.plan_error = false;
    var coach = JSON.parse(localStorage.getItem("onmytennis"));
    var coach1 = JSON.parse(coach);
    res.Coach_Id = coach1.id;
    var transport = "";
    let enableBtn = document.querySelector("#enableBtn") as HTMLElement;
    if (enableBtn.hasAttribute("disabled") == false) {
      let jsonData = res;
      let formInputItem = document
        .querySelectorAll(".form_devarea")[0]
        .querySelectorAll("input");
      formInputItem.forEach(function (inputElement) {
        let mode = inputElement as HTMLInputElement;
        if (mode.type == "checkbox") {
          if (mode.checked == true) {
            let modechild = mode.nextSibling as HTMLElement;
            if (transport !== "") {
              transport = transport + "," + modechild.nodeValue.trim();
            } else {
              transport = modechild.nodeValue.trim();
            }
          }
        }
        if (mode.type == "radio") {
          if (mode.id == "inlineradio3" && mode.checked == true) {
            jsonData.Plan = "Commission";
          } else if (mode.id == "inlineradio4" && mode.checked == true) {
            jsonData.Plan = "Abonnement";
          }
        }
      });
      res = jsonData;

      if (!res.Photo) {
        $('#photo_error').show();
      }
      if (res.Mode_of_transport == "") {
        this.trans_error = true;
        $("#trans_error").show();
      } else if (res.Plan == "") {
        $("#plan_error").show();
        this.plan_error = true;
      }
      if (this.trans_error == false && this.plan_error == false && res.Photo) {
        this.appService
          .create("/course/setcousecollectivedemanad", res)
          .subscribe(response => {
            if (response && response.isSuccess == true) {
              this._showAlertMessage("alert-success", " Mise à jour avec succès");
              this.ngOnInit();
              window.scrollTo(0, 0);
            } else {
              this._showAlertMessage("alert-danger", "Échec de la mise à jour");
            }
          });
      } else {
        this.spinner.hide();
      }
    }
  }


  //function for delete course
  deleteCourse() {
    const params = this.activatedRoute.snapshot.queryParamMap["params"];
    var parameters = {
      CoachId: params.coach_id,
      Id: params.coursedemand_id,
      course: "CoursCollectifOndemand",
    };

    this.appService
      .create("/course/deletecourse", parameters)
      .subscribe(response => {
        this.spinner.hide();
        if (response && response.isSuccess == true) {
          this._showAlertMessage("alert-success", "Supprimé avec succès");
          window.scrollTo(0, 0);
        } else {
          this.spinner.hide();
          this._showAlertMessage("alert-danger", "Échec de la mise à jour");
        }
      });

  }
  //Funution for enable and disable
  enableForm() {
    $("#availabilityDiv :input").prop("readonly", false);
    $("#availabilityDiv :input").prop("required", true);

    let itransport = document.querySelectorAll(".form-group");
    itransport.forEach(function (checkItem) {
      checkItem.removeAttribute("disabled");
    });
    $("#inlineradio3").prop("readonly", false);
    $("#inlineradio4").prop("readonly", false);

    let formInputItem = document
      .querySelectorAll(".form_devarea")[0]
      .querySelectorAll("input");
    formInputItem.forEach(function (inputElement) {
      inputElement.removeAttribute("disabled");
    });
    let textarea = document.getElementById(
      "exampleFormControlTextarea1"
    ) as HTMLTextAreaElement;
    textarea.removeAttribute("disabled");

    let Enregistrer = document.querySelector("#Enregistrer") as HTMLElement;
    Enregistrer.style.display = "inline";

    let elebebtn = document.querySelector("#enableBtn") as HTMLElement;
    elebebtn.style.display = "none";

    let icancel = document.querySelector("#cancel") as HTMLElement;
    icancel.style.display = "inline";

  }

  propagateChange = (result, file, type) => {
    this.res.Photo = result;
    this.res.filename = file.name;
  };

  changeListener($event): void {
    this.readThis($event.target);
  }

  //Function for read file extension 
  readThis(inputValue: any): void {
    var file: File = inputValue.files[0];
    $(".file-upload-wrapper").attr('data-text', file.name);
    let reader = new FileReader();
    if (file.size <= 55326 && file.type == "image/jpeg" || file.type == "image/png") {
      reader.readAsDataURL(file);
      reader.onload = _event => {
        this.propagateChange(reader.result, file, file.type);
      };
    }
    else {
      $('#file-upload-field').empty();
      $("#file-upload-field").trigger("change");
      $(".file-upload-wrapper").attr('data-text', '');
      Swal.fire('Erreur', 'Veuillez télécharger uniquement des fichiers portant les extensions .pdf/.doc/.docx et dont la taille est inférieure à 50KB.', 'error');
      reader.onerror = function (error) {
      };
      return;
    }
    reader.readAsDataURL(file);
    reader.onload = _event => {
      this.propagateChange(reader.result, file, file.type);
    };
  }
  //Function for download 
  download() {
    if (this.res.Photo) {
      var blob = this.dataURLtoBlob(this.res.Photo);
      var link = document.createElement("a");
      link.href = window.URL.createObjectURL(blob);
      link.download = this.res.filename;
      link.click();
    }
  }

  dataURLtoBlob(dataurl) {
    var arr = dataurl.split(",");
    var mime = arr[0].match(/:(.*?);/)[1];
    var bstr = window.atob(arr[1]);
    var n = bstr.length;
    var u8arr = new Uint8Array(n);
    while (n--) {
      u8arr[n] = bstr.charCodeAt(n);
    }
    return new Blob([u8arr], {
      type: mime
    });
  }

  // Funtion for transport change events
  transport_changed(event: any) {
    this.transportData = this.appService.transportChanged(event, this.transportData);
    this.res.Mode_of_transport = this.transportData.join();
    if (this.res.Mode_of_transport == "") {
      this.trans_error = true;
      $('#trans_error').show();
    } else {
      this.trans_error = false;
      $('#trans_error').hide();
    }
  }
}
